@use "base/mixins" as m;
@use "base/variables" as v;

.hero{
    &_colours{
        background: v.$gray;

        h1{
            @include m.h1;
            @media (min-width: v.$laptop){
                max-width: m.px-to-vw(400px);
            }
            @include m.laptop{
             
            }
        }

        @media (min-width: v.$laptop){
            display: grid;
            grid-template-columns: 1fr m.px-to-vw(692px);
            height: m.px-to-vw(800px);
            padding-bottom: m.px-to-vw(60px);
        }
        @include m.laptop{
            
            padding-bottom: 40px;
        }

        &>*{
            @media (min-width: v.$laptop){
                @include m.fullWH;
            }
            @include m.laptop{
                width: 100%;
            }
        }
        &-title{
            @media (min-width: v.$laptop){
                padding-left: m.px-to-vw(134px);
                padding-top: m.px-to-vw(150px);
            }
            @include m.laptop{
                padding-top: 5rem;
                padding-left: 4%;
            }
        }
        &-image{
            img{
                @include m.fullWH;
                object-fit: cover;
                object-position: center;
            }
        }
        .absolute{
            bottom: 0;
            @media (min-width: v.$laptop){
                left: m.px-to-vw(217.76px);
                width: m.px-to-vw(625.24px);
                height: m.px-to-vw(390px);
            }
            @include m.laptop{
                left: 6.5%;
                width: 500px;
                aspect-ratio: 16/9;
            }
            object-fit: cover;
            object-position: center;
        }
    }
}

.case_studies{
    @media (min-width: v.$laptop){
        padding-block: m.px-to-vw(200px) m.px-to-vw(50px);
    }
    @include m.laptop{
        padding-block: 200px 50px;
    }

    h2{
        @include m.h2center;
    }
}

%mb16{
    @media (min-width: v.$laptop){
        margin-bottom: m.px-to-vw(16px) !important;
    }
    @include m.laptop{
        margin-bottom: 12px !important;
    }
}

.wheel{
    @media (min-width: v.$laptop){
        padding-top: m.px-to-vw(200px);
    }
    @include m.laptop{
        padding-top: 200px;
    }
    h2{
        @include m.h2center;
        @extend %mb16;
    }
    h3{
        @include m.h3;
    }
    p{
        @include m.subheadingP2(v.$secondary);
    }
    &_head{
        @include m.mb70;
        p{
            text-align: center;
        }
    }
    img{
        display: block;
        width: 100%;
        @media (min-width: v.$laptop){
            max-width: m.px-to-vw(869px);
        }
        @include m.laptop{
            max-width: 789px;
        }
        margin-inline: auto;
        @include m.mb70;
    }
    &_list{
        width: 100%;
        @media (min-width: v.$laptop){
            padding-inline: m.px-to-vw(48px);
        }
        @include m.laptop{
            padding-inline: 2.1rem;
        }
        div{
            width: 100%;
            @media (min-width: v.$laptop){
                display: grid;
                grid-template-columns: 1fr m.px-to-vw(800px);
            }
            @include m.laptop{
                
            }

            &>*{
                @media (min-width: v.$laptop){
                    padding-block: m.px-to-vw(10px);
                }
                @include m.laptop{
                    padding-block: 8px;
                }
            }

            &:not(:last-child){
                @media (min-width: v.$laptop){
                    padding-bottom: m.px-to-vw(32px);
                    margin-bottom: m.px-to-vw(32px);
                    border-bottom: m.px-to-vw(1px) solid rgba($color: v.$primary, $alpha: 0.1);
                }
                @include m.laptop{
                    padding-bottom: 1.5rem;
                    margin-bottom: 1.5rem;
                    border-bottom: 1px solid rgba($color: v.$primary, $alpha: 0.1);
                }
            }
        }
    }
}

%mb48{
    @media (min-width: v.$laptop){
        margin-bottom: m.px-to-vw(48px);
    }
    @include m.laptop{
        margin-bottom: 2.1rem;
    }
}

.case_study{
    &:nth-child(odd){
        @media (min-width: v.$laptop){
            display: grid;
            grid-template-columns: m.px-to-vw(542px) 1fr;
            align-items: center;
        }
        @include m.laptop{
            
        }
        .case_study{
            &-image{
                order: -1;
            }
        }
    }
    &:nth-child(even){
        @media (min-width: v.$laptop){
            display: grid;
            grid-template-columns: 1fr m.px-to-vw(542px);
            align-items: center;
        }
        @include m.laptop{
         
        }
    }
    &>*, img{
        width: 100%;
    }
    &:not(:last-child){
        @include m.mb70;
    }
    &-info{
        @media (min-width: v.$laptop){
            padding-inline: m.px-to-vw(57px);
        }
        @include m.laptop{
            padding-inline: 2.1rem;
        }
    }
    &-nro{
        span{
            display: block;
            @include m.h8;
            @extend %mb48;
            
            @media (min-width: v.$laptop){
                padding-bottom: m.px-to-vw(48px);
                border-bottom: m.px-to-vw(1px) solid v.$primary;
            }
            @include m.laptop{
                padding-bottom: 2.1rem;
                border-bottom: 1px solid v.$primary;
            }
        }
    }
    h3{
        @include m.h3;
    }
    p, li{
        @include m.subheadingP2(v.$secondary);
    }
    .content{
        &>*{
            &:not(:last-child){
                @extend %mb48;
            }
        }
    }
}

.faq{
    @media (min-width: v.$laptop){
        padding-top: m.px-to-vw(200px);
    }
    @include m.laptop{
        padding-top: 200px;
    }
    h2{
        @include m.h2center;
    }
    &_questions{
        width: 100%;
        @media (min-width: v.$laptop){
            max-width: m.px-to-vw(712px);
        }
        @include m.laptop{
            max-width: 672px;
        }
        margin-inline: auto;
    }
    li{
        @media (min-width: v.$laptop){
            padding-block: m.px-to-vw(42px) m.px-to-vw(22px);
        }
        @include m.laptop{
            padding-block: 33px 17px;
        }
        p{
            @include m.subheadingP2(v.$secondary);
        }
        &:first-child{
            padding-top: 0 !important;
        }
        &:not(:last-child){
            @media (min-width: v.$laptop){
                border-bottom: m.px-to-vw(1px) solid v.$secondary;
            }
            @include m.laptop{
                border-bottom: 1px solid v.$secondary;
            }
        }
        .acc_head{
            @media (min-width: v.$laptop){
                @include m.flex(space-between, m.px-to-vw(50px));
                flex-wrap: nowrap;
            }
            @include m.laptop{
                @include m.flex(space-between, 2rem);
                flex-wrap: nowrap;
            }

            h3{
                @include m.h4;
            }
            svg{
                @media (min-width: v.$laptop){
                    width: m.px-to-vw(13px);
                    height: m.px-to-vw(12px);
                    object-fit: contain;
                }
            }
            @media (min-width: v.$laptop){
                padding-bottom: m.px-to-vw(20px);
            }
            @include m.laptop{
                padding-bottom: 16px;
            }
        }
    }
}

.open_to_you{
    @include m.pblock200;
    a{
        @include m.button;
        margin-inline: auto;
    }
    h2{
        @include m.h2center;
    }
    h3{
        @include m.minih3;
    }
    h2, h3{
        @extend %mb16;
    }
    &-head{
        width: 100%;
        @include m.mb70;
        @media (min-width: v.$laptop){
            max-width: m.px-to-vw(795px);
        }
        @include m.laptop{
            max-width: 700px;
        }
        margin-inline: auto;
        p{
            text-align: center;
            @include m.subheadingP2(v.$secondary);
            @media (min-width: v.$laptop){
                max-width: m.px-to-vw(576px);
            }
            @include m.laptop{
                max-width: 500px;
            }
            margin-inline: auto;
        }
    }
    &-box{
        @include m.mb70;
        @media (min-width: v.$laptop){
            display: grid;
            grid-template-columns: m.px-to-vw(630px) 1fr;
            align-items: center;
        }
        @include m.laptop{
            @include m.grid(2, 0);
            align-items: center;
        }
        &>*{
            width: 100%;
        }
    }
    &-list{
        @media (min-width: v.$laptop){
            padding-left: m.px-to-vw(48px);
        }
        @include m.laptop{
            padding-left: 2rem;
        }
        p{
            @include m.subheadingP1(v.$secondary);
        }
        div{
            &:not(:last-child){
                @media (min-width: v.$laptop){
                    margin-bottom: m.px-to-vw(38px);
                }
                @include m.laptop{
                    margin-bottom: 1.75rem;
                }
            }
        }
    }
}

%pag{
    width: 100%;
    @media (min-width: v.$laptop){
        height: m.px-to-vw(4px);
    }
    @include m.laptop{
        height: 3px;
    }
}

.clothe{
    @media (min-width: v.$laptop){
        width: m.px-to-vw(217.3px);
    }
    @include m.laptop{
        
    }

    @include m.transition;
    &>*{
        pointer-events: none;
        width: 100%;
        @include m.transition;
    }
    &_image{
        @media (min-width: v.$laptop){
            height: m.px-to-vw(600px);
            margin-bottom: m.px-to-vw(12px);
        }
        @include m.laptop{
            height: 520px;
            margin-bottom: 8px;
        }
    }
    img{
        @include m.fullWH;
        object-fit: cover;
        @include m.transition;
        object-position: top;
    }
    &_info{
        opacity: 0;
        @include m.flex(flex-start, 0);
        @media (min-width: v.$laptop){
            padding-left: m.px-to-vw(20px);
            height: m.px-to-vw(80px);
        }
        @include m.laptop{
            padding-left: 1rem;
            height: 62px;
        }
    }

    &:nth-child(1){
        .clothe{
            &_info{
                background: linear-gradient(90deg, #238640 50%, #59AA32 50%);
            }
        }
    }
    &:nth-child(2){
        .clothe{
            &_info{
                background: linear-gradient(90deg, #009689 50%, #005C70 50%);
            }
        }
    }
    &:nth-child(3){
        .clothe{
            &_info{
                background: linear-gradient(90deg, #535E0E 50%, #A89A00 50%);
            }
        }
    }
    &:nth-child(4){
        .clothe{
            &_info{
                background: linear-gradient(90deg, #009669 50%, #005C39 50%);
            }
        }
    }
}

%actived{
    @media (min-width: v.$laptop){
        width: m.px-to-vw(580px);
    }
    @include m.laptop{
        
    }

    &>*{
        opacity: 1;
    }
}

.clothes{
    @include m.pblock200;
    &_head{
        width: 100%;
        margin-inline: auto;
        @include m.mb70;
        @media (min-width: v.$laptop){
            max-width: m.px-to-vw(750px);
        }
        @include m.laptop{
            max-width: 670px;
        }
        &>*{
            text-align: center;
        }
        h2{
            @include m.h2center;
            @extend %mb16;
        }
    }
    p{
        @include m.subheadingP2;
    }
    a{
        @include m.button;
        @media (min-width: v.$laptop){
            margin-top: m.px-to-vw(24px);
        }
        @include m.laptop{
            margin-top: 18px;
        }
    }
    &_body{
        width: 100%;
    }
    &_footer{
        background: v.$gray;
        @media (min-width: v.$laptop){
            padding: m.px-to-vw(40px) m.px-to-vw(28px);
            
            display: grid;
            grid-template-columns: 1fr m.px-to-vw(488px);
            grid-gap: m.px-to-vw(134px);
        }
        @include m.laptop{
            padding: 2rem 20px;


        }
        &>*{
            width: 100%;
        }
    }
    .pagination{
        @extend %pag;
        position: relative;
        background: #F0F3F3;
        @include m.mb70;
        @media (min-width: v.$laptop){
            border-radius: m.px-to-vw(4px);
        }
        @include m.laptop{
            border-radius: 3px;
        }

        &-dot{
            position: absolute;
            @include m.centerY;
            left: 0;
            @extend %pag;
            background: v.$primary;

            @media (min-width: v.$laptop){
                max-width: m.px-to-vw(160px);
                border-radius: m.px-to-vw(2px);
            }
            @include m.laptop{
                max-width: 100px;
                border-radius: 2px;
            }
        }
    }
    &-box{
        @include m.transition;
        @include m.mb70;
        width: 100%;
        @media (min-width: v.$laptop){
            @include m.flex(center, m.px-to-vw(16px));
        }
        @include m.laptop{
            @include m.flex(center, 12px);
        }
    }
    h3{
        @include m.minih4(v.$white);
    }
    h4{
        @include m.h3;
    }

    [data-state="1"]{
        .clothe:nth-child(1){
            @extend %actived;
        }
    }
    [data-state="2"]{
        .clothe:nth-child(2){
            @extend %actived;
        }
    }
    [data-state="3"]{
        .clothe:nth-child(3){
            @extend %actived;
        }
    }
    [data-state="4"]{
        .clothe:nth-child(4){
            @extend %actived;
        }
    }
}